.promotion {
  z-index: 1;

  width: 100%;

  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.25);
  padding-bottom: 10px;

  @media (min-width: 768px) {
    max-width: calc(100% - 25px - 25px);
    height: 307px;
  }
  
  @media (min-width: 1440px) {
    max-width: none;
    height: 357px;
  }
}

.promotion__wrapper {
  height: calc(100% - 25px - 25px);

  margin-left: auto;
  margin-right: auto;
  padding-top: 25px;
  padding-bottom: 25px;
  padding-left: 25px;
  padding-right: 25px;

  @media (min-width: 1440px) {
    max-width: 1440px;

    padding-left: 165px;
    padding-right: 100px;
  }
}

.promotion__list {
  list-style: none;
  margin: 0;
  padding: 0;

  height: 100%;

  display: grid;
  grid-template-columns: auto;
  grid-auto-rows: auto;
  gap: 30px;

  @media (min-width: 768px) {
    grid-template-columns: repeat(4, auto);
    grid-template-rows: 100%;
  }
}

.promotion__item {
  position: relative;

  &:nth-child(1) {
    height: 184px;

    background: linear-gradient(360deg, rgba(82, 255, 0, 0.54) 0%, rgba(255, 184, 0, 0.47) 58.54%);
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 20px;

    @media (min-width: 768px) {
      height: auto;
      grid-column: ~"1/3";
      grid-row: 1;
    }
  }

  &:nth-child(2) {
    height: 120px;

    background: rgba(235, 126, 126, 0.49);
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 20px;

    & .promotion__item__name {
      font-size: 30px;
      line-height: 37px;

      @media (min-width: 768px) {
        font-size: 24px;
        line-height: 29px;
      }

      @media (min-width: 1440px) {
        font-size: 44px;
        line-height: 54px;
      }
    }

    @media (min-width: 768px) {
      height: auto;

      grid-column: ~"3/4";
      grid-row: 1;
    }
  }

  &:nth-child(3) {
    height: 120px;

    background: rgba(66, 255, 0, 0.5);
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 20px;

    & .promotion__item__name {
      font-size: 30px;
      line-height: 37px;

      @media (min-width: 768px) {
        font-size: 24px;
        line-height: 29px;
      }

      @media (min-width: 1440px) {
        font-size: 44px;
        line-height: 54px;
      }
    }

    @media (min-width: 768px) {
      height: auto;

      grid-column: ~"4/5";
      grid-row: 1;
    }
  }
}

.promotion__item__name {
  margin: 0;

  margin-top: 10px;
  margin-left: 10px;

  font-family: 'MA Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 47px;
  line-height: 57px;
  color: #FFFFFF;

  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(254, 198, 10, 0.25);

  @media (min-width: 768px) {
    font-size: 49px;
    line-height: 60px;

    margin-left: 30px;
    margin-top: 30px;
  }
}

.promotion__item__button {
  position: absolute;
  right: 10px;
  bottom: 10px;

  width: 47px;
  height: 47px;
  border: 3px solid #f9f9f9;
  box-sizing: border-box;
  background: #fff;
  border-radius: 50%;

  @media (min-width: 768px) {
    width: 69px;
    height: 69px;
  }
}
