.catalog {
  z-index: 1;
  width: calc(100% - @padding_on_320 - @padding_on_320);

  padding-top: 48px;
  padding-left: @padding_on_320;
  padding-right: @padding_on_320;
  padding-bottom: 41px;

  box-shadow: 0 -7px 8px rgba(0, 0, 0, 0.25);

  @media (min-width: 768px) {
    width: calc(100% - @padding_on_768 - @padding_on_768);

    padding-left: @padding_on_768;
    padding-right: @padding_on_768;
  }

  @media (min-width: 1440px) {
    width: calc(100% - @padding_on_1440 - @padding_on_1440);

    padding-left: @padding_on_1440;
    padding-right: @padding_on_1440;
  }
}

.catalog__wrapper {
  max-width: 1440px;

  margin-left: auto;
  margin-right: auto;
}

.catalog__list {
  list-style: none;
  margin: 0;
  padding: 0;

  max-width: 1300px;
  margin-left: auto;
  margin-right: auto;

  display: grid;
  justify-items: center;
  grid-template-columns: repeat(2, auto);
  grid-gap: 18px;

  @media (min-width: 768px) {
    grid-template-columns: repeat(5, auto);
    grid-gap: 21px;
  }

  @media (min-width: 1440px) {
    grid-template-columns: repeat(7, auto);
    grid-gap: 21px;
  }
}

