.position-list {
  min-width: 320px;
  width: 100%;

  @media (min-width: 768px) {
    position: relative;

    grid-column: 2;
    grid-row: 2;
  }

  @media (min-width: 1440px) {
    width: calc(100% - 25px);
  }
}

.position-list__list {
  position: relative;

  width: 100%;

  list-style: none;
  margin: 0;
  padding: 0;

  display: grid;
  grid-template-columns: repeat(auto-fill, 140px);
  justify-content: center;
  gap: 30px;

  @media (min-width: 768px) {
    grid-template-columns: repeat(auto-fill, 150px);
  }
  
  @media (min-width: 1440px) {
    grid-template-columns: repeat(auto-fill, 200px);
    gap: 40px;
  }
}

.position-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  background: #ffffff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.57);
  border-radius: 20px;

  padding-top: 15px;
  padding-left: 9px;
  padding-right: 10px;
  padding-bottom: 14px;

  &:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.57);
  }

  @media (min-width: 768px) {
    padding-top: 18.06px;
    padding-left: 12.9px;
    padding-right: 12.9px;
    padding-bottom: 11.18px;

    width: calc(150px - 12.9px - 12.9px);
    height: calc(172px - 18.06px - 11.18px);
  }

  @media (min-width: 1440px) {
    padding-top: 17px;
    padding-left: 18px;
    padding-right: 18px;
    padding-bottom: 16px;

    // margin-left: 20px;
    // margin-right: 20px;
    // margin-bottom: 20px;

    width: calc(200px - 18px - 18px);
    height: calc(200px - 17px - 16px);

    // &:nth-child(5n) {
    //   margin-right: 0;
    // }

    // &:nth-child(1),
    // &:nth-child(5n + 1) {
    //   margin-left: 0;
    // }

    // &:nth-child(n + 6) {
    //   margin-top: 20px;
    // }
  }
}

.position-list__item:nth-child(n+5) {
  display: none;
  @media (min-width: 768px) {
    display: flex;
  }
}

.position-list__name {
  margin: 0;

  font-family: "MA Bold", "Arial Narrow", sans-serif;
  font-size: 8px;
  line-height: 11px;
  text-align: center;

  color: #000000;

  margin-top: 15px;
  margin-bottom: 5px;

  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  @media (min-width: 768px) {
    margin-top: 8px;
    margin-bottom: 0;
    
    -webkit-line-clamp: 2;
  }
}

.position-list__button--before {
  grid-column: 1;
  grid-row: 1;
}

.position-list__button--after {
  grid-column: 4;
  grid-row: 1;
}

.position__img {
  width: 104px;
  height: 66;

  background: radial-gradient(50% 50% at 50% 50%, #ffef9a 22.21%, rgba(255, 255, 255, 0) 85.63%);

  @media (min-width: 768px) {
    width: 111.8px;
    height: 72.24;
  }

  @media (min-width: 1440px) {
    width: 130px;
    height: 84px;
  }
}

.position__name {
  max-width: 104px;

  margin: 0;

  font-family: "MA Bold", "Arial Narrow", sans-serif;
  font-size: 8px;
  line-height: 10px;
  text-align: center;

  color: #000000;

  margin-top: 15px;
  margin-bottom: 5px;

  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  @media (min-width: 768px) {
    max-width: none;

    margin-top: 8px;
    margin-bottom: 0;
    
    -webkit-line-clamp: 2;
  }
}

.position-list__price-place {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @media (min-width: 768px) {
    margin-top: 4px;
  }
}

.price-place__price {
  font-family: "MA Bold", "Arial Narrow", sans-serif;
  text-align: center;
  margin-top: auto;

  color: #FF0000;
  font-size: 20px;
  line-height: 24px;

  @media (min-width: 1440px) {
    font-size: 25px;
    line-height: 30px;
  }
}

.body--catalog .price-place__add__path {
  fill: #7DDB6E;
}

.body--catalog .price-place__price {
  margin-left: auto;
  margin-right: auto;

  color: #7DDB6E;
}

.price-place__add {
  @media (min-width: 768px) {
    width: 28.38px;
    height: 28.38px;
  }

  @media (min-width: 1440px) {
    width: 38px;
    height: 38px;
  }
}

.price-place__add__path {
  fill: #58C647;
}

.prodcut-in-day__button {
  background-color: transparent;
  border: 0;

  @media (min-width: 768px) {
    display: none;
  }
}