.main-catalog {
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 117px; 

  @media (min-width: 768px) {
    padding-left: 33px;
    padding-right: 33px;
    padding-bottom: 147px;
  }
}

.main-catalog__wrapper {
  width: 100%;

  margin-left: auto;
  margin-right: auto;

  display: grid;
  grid-template-columns: 100%;
  justify-items: center;

  @media (min-width: 768px) {
    grid-template-columns: 150px 1fr;
    grid-template-rows: 40px auto;
    justify-items: flex-end;
  }

  @media (min-width: 1440px) {
    max-width: 1440px;
    grid-template-columns: 225px 1fr;
  }
}

.main-catalog__loader-button {
  position: absolute;
  left: 50%;
  bottom: -72px;
  transform: translateX(-50%);

  width: 222px;
  height: 52px;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: #7DDB6E;

  border-radius: 20px;
  border: none;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.25);
  background-color: white;
}
