.searcher {
  position: relative;

  height: 35px;

  display: flex;
  flex-direction: row;

  margin-right: 27.5px;
  margin-left: auto;
  margin-bottom: -5px;

  @media (min-width: 768px) {
    flex-grow: 1;
    height: 48px;
    max-width: 600px;

    margin-left: 26px;
    margin-right: 30px;

    margin-bottom: 17px;
  }

  @media (min-width: 1440px) {
    margin-left: 20px;
    margin-right: 80px;
  }
}

.searcher__searcher {
  border: none;
  background-color: transparent;

  padding: 0;

  @media (min-width: 768px) {
    position: absolute;

    top: 10px;
    left: 10px;
  }

  @media (min-width: 1440px) {
    left: 14px;
  }
}

.searcher__searcher-img {
  width: 20px;
  height: 30px;

  @media (min-width: 768px) {
    position: absolute;

    width: 19px;
    height: 30px;

    top: 0;
    left: 0;
  }
}

.searcher__searcher-img--path {
  fill: @searcher_color;
}

.searcher__searcher-img--line {
  stroke: @searcher_color;
}

.searcher__searcher-img--mobile {
  @media (min-width: 768px) {
    display: none;
  }
}

.searcher__searcher-img--tablet {
  display: none;

  @media (min-width: 768px) {
    display: block;
  }
}

.searcher__input {
  display: none;
  outline: none;
  width: 100%;

  font-family: "MA Medium", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;

  background-color: transparent;
  color: @header_text_color;

  box-sizing: border-box;
  border-radius: 10px;

  &::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
 }
  &:-moz-placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
  &::-moz-placeholder {
    color: rgba(255, 255, 255, 0.5);
  }
  &:-ms-input-placeholder {
    color: rgba(255, 255, 255, 0.5);
  }

  @media (min-width: 768px) {
    display: block;
    border: 3px solid @searcher_color;

    font-size: 20px;
    line-height: 24px;

    padding-left: 48px;

    box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.25);

    &:hover {
      border: 3px solid white;
    }

    &:focus {
      border: 3px solid white;
    }

    &:active {
      border: 3px solid white;
    }
  }
}
