main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.body--catalog {
  background: #E2E2E2;

  & main {
    padding-top: 58px;

    @media(min-width: 768px) {
      padding-top: 114px;
    }
  }

  & .basket-right {
    top: 114px;
    padding-top: 150px - 114px;
  }

  & .position-list__item:nth-child(n+5) {
    display: block;
}
}

.half-site--left {
  width: 100%;

  @media (min-width: 1440px) {
    width: calc(100% - 322px - 30px);
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
