.row {
  position: fixed;
  background-color: #FF0A54;
  box-shadow: 0 7px 9px 3px rgba(0, 0, 0, 0.25);

  width: calc(100% - @padding_on_320 - @padding_on_320);
  z-index: 3;

  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: @padding_on_320;
  padding-right: @padding_on_320;

  @media (min-width: 768px) {
    width: calc(100% - @padding_on_768 - @padding_on_768);

    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: @padding_on_768;
    padding-right: @padding_on_768;
  }

  @media (min-width: 1440px) {
    width: calc(100% - @padding_on_1440 - @padding_on_1440);

    padding-top: 20px;
    padding-bottom: 10px;
    padding-left: @padding_on_1440;
    padding-right: @padding_on_1440;
  }

  &--catalog {
    background-color: #F80000;
  }
}

.row__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: 768px) {
    max-width: 1000px;
  }

  @media (min-width: 1440px) {
    max-width: 1440px;
  }
}

.row__left-site {
  & .burger {
    @media (min-width: 768px) {
      display: none;
    }
  }

  @media (min-width: 1440px) {
    display: block;
  }
}
