}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.catalog-button {
  display: none;
  width: 146px;
  padding-bottom: 15px;
}
@media (min-width: 768px) {
  .catalog-button {
    display: block;
  }
}
.catalog-button__button {
  width: 100%;
  height: 46px;
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 20px;
  background-color: transparent;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.catalog-button__icon {
  fill: white;
}
.catalog-button__text {
  margin: 0;
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 17px;
  line-height: 21px;
  color: white;
  margin-left: 9px;
}
.basket-right {
  display: none;
  z-index: 2;
  position: sticky;
  top: 0;
  width: 282px;
  height: calc(100vh - 150px - 20px);
  min-height: 600px;
  padding-top: 150px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 15px;
  margin-right: 30px;
  background-color: #FFFFFF;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0px 1px 2px 2px rgba(0, 0, 0, 0.25);
}
