.prodcut-in-day {
  position: relative;
  z-index: -1;

  background: #7ddb6e;

  width: 100%;

  padding-top: 42px;
  padding-bottom: 20px;

  @media (min-width: 768px) {
    width: calc(100% - @padding_on_768 - @padding_on_768);

    padding-left: @padding_on_768;
    padding-right: @padding_on_768;
    padding-top: 46px;
    padding-bottom: 42px;
  }

  @media (min-width: 1440px) {
    width: calc(100% - @padding_on_1440 - @padding_on_1440);

    padding-left: @padding_on_1440;
    padding-right: @padding_on_1440;
  }
}

.prodcut-in-day__wrapper {
  max-width: 1440px;

  margin-left: auto;
  margin-right: auto;
}

.prodcut-in-day__place-h2 {
  z-index: 1;
  position: absolute;
  top: -25px;
  left: 50%;
  transform: translateX(-50%);

  width: 199px;
  height: 54px;

  background: #ff0000;
  border-radius: 20px;

  @media (min-width: 768px) {
    left: auto;
    transform: translateX(0);
  }
}

.prodcut-in-day__h2 {
  z-index: 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);

  margin: 0;
  width: fit-content;

  font-family: "MA Bold", "Arial Narrow", sans-serif;
  text-align: center;
  vertical-align: middle;
  color: #ffffff;
}

.prodcut-in-day__list {
  list-style: none;

  margin: 0;
  padding: 0;

  display: grid;
  justify-items: center;
  grid-template-columns: 16px auto auto 16px;
  grid-template-rows: auto;
  grid-gap: 10px;

  @media (min-width: 768px) {
    grid-template-columns: repeat(5, auto);
  }
}

.product-in-day__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  background: #ffffff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.57);
  border-radius: 20px;

  padding-top: 15px;
  padding-left: 9px;
  padding-right: 10px;
  padding-bottom: 14px;

  max-width: 150px;
  height: calc(177px - 14px - 15px);

  &:hover {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.57);
  }

  @media (min-width: 768px) {
    max-width: none;

    padding-top: 18.06px;
    padding-left: 12.9px;
    padding-right: 12.9px;
    padding-bottom: 11.18px;

    width: calc(137.6px - 12.9px - 12.9px);
    height: calc(172px - 18.06px - 11.18px);
  }

  @media (min-width: 1440px) {
    padding-top: 21px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 13px;

    width: calc(160px - 15px - 15px);
    height: calc(200px - 21px - 13px);
  }
}

.product-in-day__item:nth-child(n+5) {
  display: none;
  @media (min-width: 768px) {
    display: flex;
  }
}

.product-in-day__button--before {
  grid-column: 1;
  grid-row: 1;
}

.product-in-day__button--after {
  grid-column: 4;
  grid-row: 1;
}

.position__img {
  background: radial-gradient(50% 50% at 50% 50%, #ffef9a 22.21%, rgba(255, 255, 255, 0) 85.63%);

  @media (min-width: 768px) {
    width: 111.8px;
    height: 72.24;
  }

  @media (min-width: 1440px) {
    width: 130px;
    height: 84px;
  }
}

.position__name {
  margin: 0;

  font-family: "MA Bold", "Arial Narrow", sans-serif;
  font-size: 8px;
  line-height: 11px;
  text-align: center;

  color: #000000;

  margin-top: 15px;
  margin-bottom: 5px;

  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;

  @media (min-width: 768px) {
    margin-top: 8px;
    margin-bottom: 0;
    
    -webkit-line-clamp: 2;
  }
}

.position__price-place {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  @media (min-width: 768px) {
    margin-top: 4px;
  }
}

.price-place__price {
  font-family: "MA Bold", "Arial Narrow", sans-serif;
  text-align: center;
  margin-top: auto;

  color: #7ddb6e;
  font-size: 20px;
  line-height: 24px;

  @media (min-width: 1440px) {
    font-size: 25px;
    line-height: 30px;
  }
}

.price-place__add {
  @media (min-width: 768px) {
    width: 28.38px;
    height: 28.38px;
  }
}

.price-place__add__path {
  fill: #ff0000;
}

.prodcut-in-day__button {
  background-color: transparent;
  border: 0;

  @media (min-width: 768px) {
    display: none;
  }
}