.user-menu {
  width: fit-content;

  @media (min-width: 768px) {
    margin-bottom: 17px;
  }

  @media (min-width: 1440px) {
    margin-bottom: 10px;
  }
}

.user-menu__list {
  list-style: none;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin: 0;
  padding: 0;
}

.item-menu {
  height: auto;

  &:nth-child(1),
  &:nth-child(3n) {
    margin-right: 13.5px;
  }

  &:nth-child(2n) {
    margin-left: 13.5px;
  }

  @media (min-width: 768px) {
    &:nth-child(1),
    &:nth-child(3n) {
      margin-right: 20px;
    }
  
    &:nth-child(2n) {
      margin-left: 20px;
    }
  }
}

.item-menu .link-menu {
  display: flex;
  flex-direction: сolumn;
  justify-content: space-between;
  align-items: center;

  @media (min-width: 1440px) {
    flex-direction: row;
  }
}

.link-menu__span {
  display: none;

  @media (min-width: 768px) {
    display: block;
  }
}

.link-menu {
  width: calc(100% + 5px);

  display: flex;
  flex-direction: column;
  justify-content: center;

  font-family: "MA Bold", "Arial", sans-serif;
  font-size: 11px;
  line-height: 13px;

  height: 100%;

  color: white;
  text-decoration: none;


  @media (min-width: 1440px) {
    font-size: 17px;
    line-height: 21px;
  }
}

.link-menu__img {
  height: 26px;
  width: 30.55px;

  @media (min-width: 768px) {
    height: 33px;
    width: 36px;
    padding-bottom: 5px;
  }
}

.link-menu__img--path {
  fill: @user_elements_color;
}
