.filters {
  width: 100%;

  margin-top: 20px;
  margin-bottom: 15px;

  @media(min-width: 768px) {
    margin: 0;

    width: calc(100% - 25px);
    padding-right: 25px;

    grid-column: 1;
    grid-row: 2;
  }
}

.filters__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
}

.filters__select {
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;

  height: 35px;
  width: 100%;

  flex-grow: 1;
  padding-left: 10px;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 15px;
  line-height: 18px;
  color: #393939;

  height: 35px;
  width: 100%;

  background: #FFFFFF;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  border-radius: 5px;

  &:focus-visible {
    outline: 0;
  }

  @media(min-width: 768px) {
    display: none;
  }
}

.filters__item {
  position: relative;

  display: none;
  flex-direction: row;
  align-items: center;

  margin-top: 7.5px;
  margin-bottom: 7.5px;
  margin-left: 9px;

  &::before {
    position: absolute;
    left: 0;
    bottom: -7px;

    content: "";

    width: 100%;
    height: 1px;

    background-color: #C7C7C7;

    @media (min-width: 1440px) {
      left: -9px;
    }
  }

  &:last-child {
    &::before {
      display: none;
    }
  }

  @media(min-width: 768px) {
    display: flex;
  }
}

.filters__item__icon {
  display: none;

  width: 30px;
  height: 30px;

  margin-right: 10px;

  @media (min-width: 1440px) {
    display: block;
  }
}

.filters__item__name {
  position: relative;
  margin: 0;

  font-family: 'Oswald Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #6D6D6D;

  text-align: center;

  @media (min-width: 1440px) {
    font-size: 17px;
    line-height: 25px;
  }

  // &::before {
