.sort {
  height: fit-content;
  width: 100%;

  margin-bottom: 25px;

  @media(min-width: 768px) {
    margin: 0;

    width: auto;

    grid-column: 2;
    grid-row: 1;
  }
}

.sort__list {
  list-style: none;
  padding: 0;
  margin: 0;

  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    flex-direction: row;
  }
} 

.sort__list__wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 15px;
  line-height: 18px;
  color: #393939;

  background: #FFFFFF;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  border-radius: 5px;

  margin-top: 15px;

  &:first-child {
    margin-top: 0;
  }

  &--pokas {
    padding-left: 10px;
  }

  &--pokas .sort__select{
    width: fit-content;

    margin-right: auto;
  }

  @media(min-width: 768px) {
    display: none;
  }
}

.sort__select {
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;

  flex-grow: 1;

  height: 35px;
  width: 100%;

  padding-left: 10px;

  &:focus-visible {
    outline: 0;
  }
}



.sort__label {
  margin: 0;
}

.sort__item {
  display: none;
  flex-direction: row;
  align-items: center;

  margin-left: 20px;
  margin-right: 20px;

  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    margin-right: 0;
  }

  @media(min-width: 768px) {
    display: flex;
  }
}

.sort__name {
  margin: 0;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 15px;
  line-height: 18px;

  margin-right: 5px;
}
