.shop-list {
  display: none;
  position: absolute;
  z-index: 1;

  flex-direction: column;

  background-color: white;

  overflow-y: scroll;

  @media (min-width: 768px) {
    display: block;

    width: 255px;
    max-height: 550px;
  }
}

.shop-list__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.shop-list__item {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @media (min-width: 768px) {
    height: calc(114px - 10px - 10px);

    padding: 10px;
  }
}

.shop-list__img {
  background-image: url("../img/background-shop.webp");;
}

.shop-list__address,
.shop-list__operating {
  font-family: 'Oswald Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 14px;
  line-height: 21px;
}