.nav {
  width: calc(100% - 5px);

  margin-left: auto;
  margin-right: auto;
  padding-top: 10px;
  padding-left: 5px;

  @media (min-width: 768px) {
    width: calc(100% - 60px);
    max-width: 1700px;

    padding-top: 20px;
    padding-left: 60px;
  }
}

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: row;
}

.nav__item {
  position: relative;

  margin-left: 17.5px;
  margin-right: 17.5px;

  &::before {
    position: absolute;

    top: 50%;
    transform: translateY(-50%);
    right: -19px;

    content: "";
    
    width: 7px;
    height: 12px;
    background-image: url("../img/nav-arrow.webp");
  }

  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    margin-right: 0;

    &::before {
      display: none;
    }
  }
}

.nav__item__link {
  text-decoration: none;

  font-family: 'Oswald Regular', 'Arial Narrow', Arial, sans-serif;
  color: #A0A0A0;
  font-size: 12px;
  line-height: 18px;

  @media (min-width: 768px) {
    font-size: 12px;
    line-height: 18px;
  }
}
