footer .background {
  z-index: -1;

  position: absolute;
  width: 100%;
  height: 122.99px;
  background-color: #333333;
}

.footer {
  background-color: #333333;

  @media (min-width: 1440px) {
    margin-top: 40px;
  }
}

.footer__wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  width: calc(100% - 48px - 48px);
  padding: 17px 48px 20px 48px;

  @media (min-width: 768px) {
    align-items: flex-start;
    flex-direction: row;

    width: calc(100% - 15px - 32px);
    padding: 15px 15px 15px 32px;
  }

  @media (min-width: 1440px) {
    margin-left: auto;
    margin-right: auto;
    padding: 15px 32px 24px 32px;
  }
}

.footer__links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  width: fit-content;

  margin-bottom: 15px;
  margin-left: auto;
  margin-right: auto;

  @media (min-width: 768px) {
    width: auto;
    flex-direction: row;

    margin-bottom: 0;
  }
}

.footer__links__icon path {
  fill: white;
}

.footer__links__list {
  list-style: none;
  margin: 0;
  padding: 0;

  &:nth-child(1) {
    margin-bottom: 25px;
  }

  &:nth-child(2) .footer__links__item::before {
    content: "";
    position: absolute;
    bottom: -15px;
    left: -29px;

    width: 225px;
    height: 1px;

    background-color: #A3A3A3;

    @media (min-width: 768px) {
      display: none;
    }
  }

  @media (min-width: 768px) {
    &:nth-child(1) {
      margin-right: 15px;
    }
    &:nth-child(2) {
      margin-left: 15px;
    }
  }
}

.footer__logo {
  order: 3;
  margin-top: 15px;
  max-width: 200px;

  @media (min-width: 768px) {
    order: 0;
    margin: 0;

    margin-left: 0;
    margin-right: 30px;
  }
}

.footer__logo .logo__img {
  display: block;
  margin: 0;
  padding: 0;

  width: 200px;
  height: 84px;
}

.footer__logo .logo__img__g {
  fill: white;;
}

.footer__links__item {
  position: relative;

  display: none;

  &.footer__links__item--mobile {
    display: block;

    @media (min-width: 768px) {
      display: none;
    }
  }

  @media (min-width: 768px) {
    display: block;
  }
}

.footer__links__link {
  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  color: white;
  font-size: 15px;
  line-height: 18px;

  @media (min-width: 768px) {
    font-size: 10px;
    line-height: 12px;
  }
}

.social__list {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;

  @media (min-width: 768px) {
    align-items: flex-end;
  }
}

.social__item--tel {
  position: relative;
  margin-top: 15px;
  margin-bottom: 15px;

  &::before {
    content: "";
    position: absolute;
    bottom: -15px;
    left: -50px;

    width: 225px;
    height: 1px;

    background-color: #a3a3a3;

    @media (min-width: 768px) {
      display: none;
    }
  }

  &::after {
    position: absolute;
    content: "Круглосуточная поддержка клиентов";
    bottom: -6px;
    left: -13px;

    font-family: 'MA Bold','Arial Narrow',Arial,sans-serif;
    font-size: 7px;
    line-height: 9px;

    width: 175px;

    color: white;
  }

  & .social__link {
    margin-left: -25px;
  }

  @media (min-width: 768px) {
    padding-right: 25px;
    margin-top: 0;
    margin-bottom: 15px;
  }
}

.social__icon {
  position: absolute;
  right: -23px;
  top: 1px;

  @media (min-width: 768px) {
    right: 0;
  }
}

.social__icon__path {
  fill: white;
}

.social__link {
  text-decoration: none;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 20px;
  line-height: 24px;
  color: white;
}

.socials__list {
  position: relative;
  list-style: none;

  width: 132px;

  margin: 0;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 0;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  &::before {
    content: "";
    position: absolute;
    bottom: -15px;
    left: -50px;

    width: 225px;
    height: 1px;

    background-color: #A3A3A3;

    @media (min-width: 768px) {
      display: none;
    }
  }

  @media (min-width: 768px) {
    width: auto;
    margin: 0;
    justify-content: right;
  }
}

.socials__item {
  @media (min-width: 768px) {
    &:nth-child(1) {
      margin-right: 7.5px;
    }
    &:nth-child(2) {
      margin-left: 7.5px;
      margin-right: 7.5px;
    }
    &:nth-child(3) {
      margin-left: 7.5px;
    }
  }
}
