.basket-right {
  display: none;
  z-index: 2;
  position: sticky;
  top: 0;

  width: 282px;
  height: calc(100vh - 150px - 20px);
  min-height: 600px;

  padding-top: 150px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 15px;
  margin-right: 30px;
  
  background-color: #FFFFFF;
  border-radius: 0px 0px 10px 10px;
  box-shadow: 0px 1px 2px 2px rgba(0, 0, 0, 0.25);

  @media (min-width: 1440px) {
    display: block;
  }
}

.basket-right__wrapper {
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.basket-right__arrow {
  border: none;

  position: absolute;
  top: 45px;
  left: -38px;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 38px;
  height: 24px;

  background: #58FF91;
  border-radius: 13px 0px 0px 13px;
}

.basket-right__arrow__svg {
  height: 11px;
  width: 17px;
}

.basket-right__arrow__path {
  fill: white;
}

.basket-right__info {
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.basket-position-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.basket-right__message-empty-basket {
  margin: 0;

  width: fit-content;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  line-height: 16px;
  color: #9F9F9F;
}

.pluses-list {
  list-style: none;
  margin: 0;
  margin-top: auto;
  margin-bottom: auto;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.pluses-list__item {
  width: 250px;
  height: 100px;

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  margin-top: 37.5px;
  margin-bottom: 37.5px;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.pluses-list__item__img {
  width: 82px;
  height: 82px;
}

.pluses-list__item__name {
  margin: 0;

  width: 155px;

  font-family: 'Oswald Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 20px;
  line-height: 19px;
}

.pluses-list__item__description {
  font-family: 'Oswald Regular', 'Arial Narrow', Arial, sans-serif;
  font-size: 13px;
  line-height: 19px;
  color: #959595;

  margin-top: 5px;

  width: 142px;
}

.basket-right__button {
  width: 216px;
  height: 46px;

  margin-top: auto;

  border: 0;
  background: #E2E2E2;
  box-shadow: 0px 2px 0px rgba(255, 214, 0, 0.93);
  border-radius: 20px;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 11px;
  line-height: 13px;
  color: #000000;
}
