.catalog-button {
  display: none;
  width: 146px;
  padding-bottom: 15px;

  @media (min-width: 768px) {
    display: block;
  }
}

.catalog-button__button {
  width: 100%;
  height: 46px;

  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 20px;

  background-color: transparent;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.catalog-button__icon {
  fill: white;
}

.catalog-button__text {
  margin: 0;

  font-family: 'MA Bold', 'Arial Narrow', Arial, sans-serif;
  font-size: 17px;
  line-height: 21px;
  color: white;

  margin-left: 9px;
}
